.container {
  margin: 0 auto;
  max-width: 1366px;
  padding: 0 98px;

  @media (max-width: $desktop-width) {
    padding: 0 45px;
  }

  @media (max-width: $tablet-width) {
    min-width: 320px;
    padding: 0 15px;
  }
}

