html {
  height: 100%;
  scroll-behavior: smooth; }

img {
  max-width: 100%;
  height: auto; }

.list {
  list-style-type: none;
  padding-left: 0; }

h1,
h2,
h3 {
  font-family: "Cormorant Garamond", "Times New Roman", serif; }

p {
  font-family: "Poppins", "Arial", sans-serif; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
::after,
::before {
  box-sizing: border-box; }

.container {
  margin: 0 auto;
  max-width: 1366px;
  padding: 0 98px; }
  @media (max-width: 1023px) {
    .container {
      padding: 0 45px; } }
  @media (max-width: 767px) {
    .container {
      min-width: 320px;
      padding: 0 15px; } }

.page {
  height: 100%;
  font-family: "Poppins", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  margin: 0;
  display: flex;
  flex-direction: column; }

.page__wrapper {
  flex: 1 0 auto; }

.page__header {
  padding-top: 30px; }
  @media (max-width: 1023px) {
    .page__header {
      padding-top: 17px; }
      .page__header.page__opened, .page__header.page__nojs {
        content: "";
        display: block;
        position: absolute;
        min-height: 105vh;
        width: 100%;
        top: 0;
        border: none;
        background-color: #a87b62; } }
  @media (max-width: 1023px) and (max-width: 767px) {
    .page__header {
      padding-top: 20px; } }

.page__nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative; }

.page__logo {
  order: 2;
  margin-left: auto;
  margin-bottom: 19px; }
  @media (max-width: 1023px) {
    .page__logo {
      margin: 0 auto; } }

.page__logo svg {
  fill: #a87b62; }
  @media (max-width: 1023px) {
    .page__logo svg {
      width: 52px;
      height: 52px; } }

@media (max-width: 1023px) {
  .page__opened svg,
  .page__nojs svg {
    fill: #ffffff; } }

.page__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .page__toggle {
      content: "";
      display: block;
      position: absolute;
      height: 20px;
      width: 28px;
      background-color: transparent;
      cursor: pointer;
      border: none;
      top: 17px;
      left: 45px;
      padding: 0; }
      .page__toggle::before {
        content: "";
        display: block;
        position: absolute;
        top: 3px;
        width: 25px;
        height: 2px;
        background-color: #1d1613;
        box-shadow: 0 5px 0 0 #1d1613, 0 10px 0 0 #1d1613; }
        .page__opened .page__toggle::before,
        .page__nojs .page__toggle::before {
          background-color: #ffffff;
          box-shadow: 0 5px 0 0 #ffffff, 0 10px 0 0 #ffffff; } }

.page__search {
  order: 1;
  color: #9b9997;
  position: relative; }
  @media (max-width: 1023px) {
    .page__opened .page__search,
    .page__nojs .page__search {
      width: 100%;
      order: 5;
      margin-top: 40px;
      margin-bottom: 55px; }
      .page__opened .page__search svg,
      .page__nojs .page__search svg {
        position: absolute;
        top: 12px;
        left: 12px; }
    .page__closed .page__search {
      display: none; } }
  .page__search input {
    border: none; }
    @media (max-width: 1023px) {
      .page__search input {
        width: 100%;
        min-height: 40px;
        border-radius: 21px;
        background-color: #ffffff;
        opacity: 0.15; }
        .page__search input::placeholder {
          color: transparent; } }

.page__login {
  order: 3;
  margin-left: auto; }
  @media (max-width: 1023px) {
    .page__opened .page__login,
    .page__nojs .page__login {
      font-size: 18px;
      line-height: 23px;
      font-weight: 500;
      color: #ffffff;
      order: 10;
      position: absolute;
      bottom: calc(100% - 650px);
      left: 45px; }
    .page__closed .page__login {
      display: none; } }
  @media (max-width: 767px) {
    .page__opened .page__login,
    .page__nojs .page__login {
      left: 15px; } }

.page__cart {
  order: 4;
  margin-left: 35px; }
  @media (max-width: 1023px) {
    .page__cart {
      content: "";
      display: block;
      position: relative;
      content: url("data:image/svg+xml,%3Csvg width='20' height='18' viewBox='0 0 20 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 5C14 6.06087 13.5786 7.07828 12.8284 7.82843C12.0783 8.57857 11.0609 9 10 9C8.93913 9 7.92172 8.57857 7.17157 7.82843C6.42143 7.07828 6 6.06087 6 5M1 1H19V17H1V1Z' stroke='%231D1613' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      width: 19px;
      height: 18px;
      top: 2px;
      left: 2px;
      margin-left: 0; }
      .page__opened .page__cart,
      .page__nojs .page__cart {
        content: url("data:image/svg+xml,%3Csvg width='20' height='18' viewBox='0 0 20 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 5C14 6.06087 13.5786 7.07828 12.8284 7.82843C12.0783 8.57857 11.0609 9 10 9C8.93913 9 7.92172 8.57857 7.17157 7.82843C6.42143 7.07828 6 6.06087 6 5M1 1H19V17H1V1Z' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
        width: 19px;
        height: 18px;
        top: 17px;
        right: 0;
        left: unset; } }

.page__login,
.page__cart,
.page__search {
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  padding-top: 17px;
  cursor: pointer; }
  @media (max-width: 1023px) {
    .page__login,
    .page__cart,
    .page__search {
      padding-top: 0; } }
  .page__login:hover, .page__login:focus,
  .page__cart:hover,
  .page__cart:focus,
  .page__search:hover,
  .page__search:focus {
    opacity: 0.5; }

.page__login,
.page__cart {
  color: #1d1613; }

.page__footer {
  padding-top: 48px;
  flex-shrink: 0; }
  @media (max-width: 1023px) {
    .page__footer {
      padding-top: 6px; } }

.page__hidden {
  overflow: hidden; }

.info {
  color: #ffffff;
  background-image: url("../img/intro-main-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: 100% 100%;
  background-color: #a87b62;
  overflow: hidden; }
  @media (min-resolution: 2dppx) {
    .info {
      background-image: url("../img/intro-main-desktop-2x.jpg"); } }

.info__wrapper {
  margin: 0 auto; }

.info-list {
  display: none; }
  @media (max-width: 1023px) {
    .info-list {
      display: block; } }

.info-list__link {
  font-size: 18px;
  line-height: 23px;
  font-weight: 500;
  color: #ffffff;
  text-decoration: none; }

.site-list {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  column-gap: 35px;
  padding-top: 18px;
  padding-bottom: 19px; }
  @media (max-width: 1023px) {
