      line-height: 36px;
      font-weight: 500;
      color: #ffffff;
      padding-bottom: 20px; } }
  .site-list__link:hover, .site-list__link:focus {
    opacity: 0.5; }

.main-nav {
  border-top: 1px solid #f6f5f4;
  border-bottom: 1px solid #f6f5f4; }
  @media (max-width: 1023px) {
    .main-nav {
      border: none; } }

.main-nav__wrapper {
  display: flex;
  flex-direction: row; }
  @media (max-width: 1023px) {
    .main-nav__wrapper {
      flex-direction: column; } }
